<template>
  <nav :class="'navbar bg-' + background_color">
    <div class="container">
      
      <div class="navbar-brand bars" v-if="sidebar" @click="toggled()">
        <span style="display: -webkit-inline-box;">
          <font-awesome-icon :class="'nav-icon text-' + color" :icon="['fa', 'bars']" />
        </span>
      </div>

      <profile></profile>

      <div class="divider mt-4 d-none"></div>
    </div>
  </nav>
</template>

<style lang="scss" scoped>
    @import '../assets/scss/layout/_navbar.scss';

    .dropdown-item {
      cursor: pointer;
    }
    
</style>

<script>
import Profile from '../components/Profile';


export default {
    name: 'Navbar',
    props: {
      background_color: {
        type: String,
        required: true
      },
      sidebar: {
        required: false,
        type: Boolean
      }
    },
    components: {
      Profile
    },
    data() {
      return {
        color: ''      
      }
    },
    mounted() {
      this.setColor();
    },
    methods: {
      toggled() { 
        document.querySelector('#sidebar-wrapper').classList.add('toggled');
        document.querySelector('.backdrop').classList.add('on');
      },
      setColor() {
        if(this.background_color == "white") {
          this.color = 'muted'
        } else {
          this.color = 'white'
        }
      }
    }
}
</script>